
import React, { useState, useEffect, useRef } from "react"
import { Link } from "gatsby";
import favicon from '../../images/tw.svg';
import { MdClose, MdMenu } from 'react-icons/md';

import dimensionChangeListener from "../DimensionChangeListener";
import {DIMENSIONS} from "../DimensionChangeListener/component";

import './_header.scss';

const links = [{
    path: "/",
    text: "Home"
}, {
    path: "/blogs/computer_science",
    text: "Computer Science"
}, {
    path: "/blogs/photography",
    text: "Photography"
}, {
    path: "/store/merch",
    text: "Merch"
}];

const Header = () => {
    const [isOpen, setIsOpen] = useState(null);
    const navRef = useRef({
        location: null,
    });

    const onDimensionChange = (size) => {
        if (isOpen && size === DIMENSIONS.DESKTOP) {
            toggleState(false);
        }
    };

    useEffect(() => {
        dimensionChangeListener.attach(onDimensionChange);
        const location = window.location;
        if (!navRef.current.location) {
            navRef.current.location = location;
        } else if (navRef.current.location !== location) {
            if (isOpen) {
                toggleState(false);
            }
            navRef.current.location = location;
        }
        return () => {
            dimensionChangeListener.detach(onDimensionChange);
        }
    })
    const toggleState = (status) => {
        setIsOpen(status)
    };

    const openNavigation = () => toggleState(true);
    const closeNavigation = () => toggleState(false);

    return (
        <header className="header" ref={navRef}>
            <nav>
                <ul className="header-nav-list">
                    <li>
                        <Link to={`/`}><img src={favicon} alt="TechWaifu" /> </Link>
                    </li>
                    {links.map(link => {
                        return (
                            <li className="NavItem DesktopNav" key={link.path}>
                                <Link to={link.path}>{link.text}</Link>
                            </li>
                        );
                    })}
                </ul>

                <ul className="header-nav-list right-side">
                    <li className="NavItem">
                        <button onClick={openNavigation} aria-label="Open Navigation Menu"><MdMenu size={"2.5em"} /></button>
                    </li>
                </ul>
            </nav>
            {isOpen &&
                <div className="MobileNavigationCtr">
                    <nav>
                        <button className="CloseNavButton" onClick={closeNavigation} aria-label="Close Navigation Menu"><MdClose size={"2.5em"} /></button>
                        <ul className="header-nav-list">
                            {links.map(link => {
                                return (
                                    <li className="NavItem" key={link.path}>
                                        <Link to={link.path}>{link.text}</Link>
                                    </li>
                                );
                            })}
                        </ul>
                    </nav>
                </div>
            }
        </header>
    );
}

export default Header;
