import React, { useEffect, useCallback, useState, useRef } from "react";

import dimensionChangeListener from "../DimensionChangeListener";
import {debounce} from "../../throttle/debounce";
import queryString from "query-string";
import "./styles.scss";

const XXS_WIDTH = 240;
const XS_WIDTH = 320;
const S_WIDTH = 480;
const MED_WIDTH = 720;
const MAX_WIDTH = 960;
const compressedSizes = [
    XXS_WIDTH,
	XS_WIDTH,
	S_WIDTH,
	MED_WIDTH,
	MAX_WIDTH,
];

function createSrcSet(url, isResponsive, supportsWebp, srcSetOverride) {
    let urlWithoutParams = url;
    if (url.indexOf("?") >= 0) {
        urlWithoutParams = url.substring(0, url.indexOf("?"));
    }
    const hiResImg = urlWithoutParams;

    let srcsetUrl = null;
    let webpSrcsetUrl = null;
    const parts = hiResImg.split(".");
    let lowResImg = hiResImg;
    if (isResponsive) {
        if (srcSetOverride) {
            srcsetUrl = srcSetOverride;
        } else {
            srcsetUrl = compressedSizes.reduce((cum, size) => {
                const url = parts[0] + "-" + size + "." + parts[1];
                if (size === XXS_WIDTH) {
                    lowResImg = url;
                }
                const srcSetUrl = `${url} ${size}w`;
                if (cum === "") {
                    return srcSetUrl;
                }

                return cum + ", " + srcSetUrl;
            }, "");
        }

        if (supportsWebp) {
            webpSrcsetUrl = srcsetUrl.split(".jpg").join(".webp");
        }
    }

    return {
        lowResImg: lowResImg,
        hiResImg: hiResImg,
        srcsetUrl,
        webpSrcsetUrl
    };
}

function generateClass(className, loaded, shouldStartLazyLoadAnimation) {
    let classes = "";
    if (className) {
        classes += className;
    }
    if (shouldStartLazyLoadAnimation) {
        classes += " LazyLoadAnimation";
    }
    return classes.trim();
}

const placeholderSrc = (img) => {
    const { width, height } = img;
    if (!width || !height) {
        return `data:image/svg+xml,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 960 960"%3E%3C/svg%3E`;
    }

    return `data:image/svg+xml,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${width} ${height}"%3E%3C/svg%3E`;
};

const LazyLoadImage = (props) => {
    const ref = useRef(null);

    const [state, setState] = useState({
        loaded: false,
        src: placeholderSrc(props),
        srcSet: null, 
        webpSrcSet: null,
        shouldFetch: true,
        shouldStartLazyLoadAnimation: false,
    });

    const removeListeners = () => {
        window.removeEventListener("scroll", handleScroll);
    };

    const removeListenersCallback = useCallback(removeListeners);

    const loadImage = useCallback (() => {
        setState({
            ...state,
            src: props.src,
            shouldFetch: false,
            srcSet: props.srcSet,
            shouldStartLazyLoadAnimation: true,
            webpSrcSet: props.webpSrcSet,
        });
        removeListenersCallback();
    }, [state, removeListenersCallback, props]);

    const shouldLoadImage = useCallback((componentOffsetTop, userYPosition) => {
        return state.shouldFetch &&
        !state.loaded &&
        componentOffsetTop <= userYPosition + document.body.clientHeight + 500; 
    }, [state]);

    const onImageLoad = () => {
        if (!state.loaded && state.src === props.src) {
            setState({
                ...state,
                loaded: true,
                shouldStartLazyLoadAnimation: false,
            });
        }
    };

    const onError = () => {
        setState({
            ...state,
            loaded: true
        });
        removeListeners();
    };

    const handleScroll = useCallback(debounce(() => {
        const yPosition = window.scrollY;
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
        }
    }, 100), [loadImage, shouldLoadImage]);

    useEffect(() => {
        const onDimensionChange = () => {
            const yPosition = window.scrollY;
            if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
                loadImage();
            }
        };

        dimensionChangeListener.attach(onDimensionChange);

        const yPosition = window.scrollY;
        // Handles initial mount to check if image is already within threshold without scrolling.
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
        } else {
            window.addEventListener('scroll', handleScroll);
        }

        return () => {
            removeListenersCallback();
            dimensionChangeListener.detach(onDimensionChange);
        };
    }, [shouldLoadImage, loadImage, handleScroll, removeListenersCallback]);

    const { loaded, src, srcSet, webpSrcSet  } = state;
    const { sizes } = props;
    return (
        <>
            <picture>
                {webpSrcSet &&
                    <source type="image/webp" srcSet={webpSrcSet} sizes={sizes} />
                }
                <img
                    ref={ref}
                    onLoad={onImageLoad}
                    onError={onError}
                    className={generateClass(props.className, loaded, state.shouldStartLazyLoadAnimation)}
                    src={src}
                    srcSet={srcSet}
                    sizes={sizes}
                    alt={props.alt}
                    loading="lazy"
                />
            </picture>
            <noscript>
                <img src={props.src} alt={props.alt} loading="lazy" />
            </noscript>
        </>
    );
}

const LazyLoadImageWithDimensionsWrapper = (props) => {
    const { url, isResponsive, srcSet: srcSetOverride, sizes, supportsWebp } = props;
    const {lowResImg, srcsetUrl, webpSrcsetUrl} = createSrcSet(url, isResponsive, supportsWebp, srcSetOverride);

    const srcSetSizes = sizes ? sizes : "(max-width: 480px) 200px";
    return (
        <LazyLoadImage
            {...props}
            sizes={srcSetSizes}
            src={lowResImg}
            srcset={srcsetUrl}
            webpSrcSet={webpSrcsetUrl}
        />
    );
}

const Img = (props) => {
    const { src } = props;
    const sepIdx = src.indexOf("?");

    if (sepIdx >= 0) {
        const params = queryString.parse(src.substring(sepIdx));
        if (params.height && params.width) {
            const options = props.options ? props.options : {};
            const img = {
                url: src,
                height: params.height,
                width: params.width,
                hiResUrl: params.hiRes,
                isResponsive: params.isResponsive,
                alt: props.alt,
                isNotExpandable: params.isNotExpandable,
                supportsWebp: params.supportsWebp,
                srcSet: params.srcSet,
                sizes: params.sizes,
                ...options,
            }
            return (
                <div className="LazyLoadImageWrapper">
                    <LazyLoadImageWithDimensionsWrapper
                        {...props}
                        {...img}
                    />
                </div>
            );
        }
    }

    return (
        <div className="LazyLoadImageWrapper">
            <LazyLoadImage {...props} />
        </div>
    );
};

export default Img; 